<!--  -->
<template>
  <div class="d-flex flex-column">
    <CommonOptions @OnQueryBtnClick="HandleQueryBtnClick"></CommonOptions>
    <div class="w-100 m-1 border rounded p-2">
      <b-pagination
        :per-page="QueryResult.DataNumPerPage"
        :total-rows="QueryResult.TotalDataNum"
        aria-controls="measure_result_table"
        class="pagination justify-content-start"
        v-model="QueryResult.Page"
        @click="PageChnageHandle"
      ></b-pagination>
      <el-table
        border
        :data="QueryResult.dataList"
        style="width:100%;height:500px"
        id="measure_result_table"
        empty-text=""
      >
        <el-table-column prop="TaskName" label="" fixed="left" width="210"></el-table-column>
        <el-table-column prop="AGVName" label="AGV" fixed="left" width="90"></el-table-column>
        <el-table-column prop="BayName" label="Bay" fixed="left" width="90"></el-table-column>
        <el-table-column prop="location" label="" fixed="left"></el-table-column>
        <el-table-column
          :formatter="TimeFormatter"
          prop="StartTime"
          label=""
          fixed="left"
          width="180"
        ></el-table-column>
        <el-table-column :formatter="ResultFormatter" prop="result" label="" fixed="left"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="illuminance" label="(lux)"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="decibel" label="(dB)"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="temperature" label=" ()"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="humudity" label=" (%)"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="IPA" label="IPA ()"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="Acetone" label="Acetone ()"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="TVOC" label="TVOC (Non-Target)"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="PID" label="PID"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="partical_03um" label="(0.3m)"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="partical_05um" label="(0.5m)"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="partical_10um" label="(1.0m)"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="partical_30um" label="(3.0m)"></el-table-column>
        <el-table-column :formatter="ValFormatter" prop="partical_50um" label="(5.0m)"></el-table-column>
        <el-table-column
          :formatter="ValFormatter"
          prop="partical_100um"
          label="(10.0m)"
          fixed="right"
        ></el-table-column>
      </el-table>
    </div>
    <trendChart class="flex-fill"></trendChart>
  </div>
</template>

<script>
import CommonOptions from './CommonOptions'
import { Query } from '@/api/MeasureResultAPI'
import moment from 'moment'
import trendChart from './Components/InsMesTrendChart.vue'
export default {
  components: { CommonOptions, trendChart },
  data() {
    return {
      QueryResult: {

      },
      Options: {}
    };
  },
  computed: {},
  watch: {},
  methods: {
    async HandleQueryBtnClick(options) {
      this.Options = options;
      this.QueryResult = await Query(options)
    },
    ResultFormatter(row, column, cellValue, index) {
      return cellValue == 'done' ? '' : ''
    },
    TimeFormatter(row, column, cellValue, index) {
      return moment(cellValue).format('yyyy-MM-DD \r\n HH:mm:ss')
    },
    ValFormatter(row, column, cellValue, index) {
      return cellValue == '-1' || cellValue == '-1.0' ? 'NA' : cellValue;
    },
    PageChnageHandle() {
      this.Options.Page = this.QueryResult.Page;
      this.HandleQueryBtnClick(this.Options);
    }
  },
  mounted() {

  },
}
</script>
<style lang='scss' scoped></style>